.skill-bubble {
    border-radius: 12px;
    background-color: #FF9800;
    padding: 1px 10px;
    color: #fff;
    display: inline-block;
    font-size: 12px;
    margin-right: 2px;
    max-width: 100px;
}

.truncate {
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}



