.workCanvasContainer {
    background: #fff;
    width: calc(100% - 366px);
    padding: 20px;
    border-radius: 4px;
    height: calc(100% - 66px);
    position: absolute;
    right: 12px;
    top: 12px;
    overflow-y: auto;
    border: 1px solid #d8dde6;
}


